    function getMentionsByCity() {
      vm.listeningAnalysisService.getMentionsByCity(vm.query, user.token).then(function(res){

         var mapData = angular.copy(res.data.ppulse.data.citydata);
         vm.mapData = angular.copy(res.data.ppulse.data.citydata);
          //var mapData = [{"code":"AD" , "name":"Afghanistan", "value":32358260, "color":"#eea638"}];

          // get min and max values
          var minBulletSize = 10;
          var maxBulletSize = 20;
          var min = Infinity;
          var max = -Infinity;
          for ( var i = 0; i < mapData.length; i++ ) {
            var value = mapData[ i ].mentions;
            if ( value < min ) {
              min = value;
            }
            if ( value > max ) {
              max = value;
            }
          }

          // it's better to use circle square to show difference between values, not a radius
          var maxSquare = maxBulletSize * maxBulletSize * 2 * Math.PI;
          var minSquare = minBulletSize * minBulletSize * 2 * Math.PI;

          // create circle for each country
          var images = [];
          for (i = 0; i < mapData.length; i++ ) {
            var dataItem = mapData[ i ];
            var value = dataItem.mentions;
            // calculate size of a bubble
            var square = ( value - min ) / ( max - min ) * ( maxSquare - minSquare ) + minSquare;
            if ( square < minSquare ) {
              square = minSquare;
            }
            var size = Math.sqrt( square / ( Math.PI * 2 ) );
            // var id = dataItem.code;

            images.push({
              "type": "circle",
              "theme": "light",
              "width": size,
              "height": size,
              "color": '#7CB5EC',
              "longitude": dataItem.longitude,
              "latitude": dataItem.latitude,
              "title": dataItem.name +' ' +value,
              "value": value
            });
          }

          // build map
          var map = AmCharts.makeChart( "mapChartdiv", {
            "type": "map",
            "projection": "eckert6",
            "dragMap": false,
            "titles": [ {
              "text": "",
              "size": 18,
              "bold": false
            }],
            "areasSettings": {
              //"unlistedAreasColor": "#000000",
              //"unlistedAreasAlpha": 0.1
            },
            "zoomControl": {
          		"zoomControlEnabled": false,
              "homeButtonEnabled" : false
          	},
            "dataProvider": {
              "map": "pakistanLow",
              "images": images
            },
            "export": {
              "enabled": true
            }
          });

      }).catch(function(err){
        $log.log(err);
      });
    }

    function getTopics() {
      vm.insightService.getTopics(vm.query, user.token).then(function(res){
        // vm.processing++;
        var treeMapOptions = {
          colorAxis: {
              minColor: '#EFF6FD',
              maxColor: Highcharts.getOptions().colors[0]
          },
          series: [],
          title: {
              text: 'Topics'
          }
        };
        var topics = angular.copy(res.data.ppulse.data);

        var treemapData = [],
        colorCode = 0;

        topics.sort(function(a, b) { return parseFloat(b.score) - parseFloat(a.score); });

        for(var i = 0;i<topics.length;i++){
          try{
            if(topics[i].score !== topics[i-1].score)
              colorCode++;
          } catch(e){
            //$log.log('First Topic');
          }
          treemapData.push({'name': topics[i].topic, 'value': topics[i].score, 'colorValue': colorCode});
        }

        treeMapOptions.series.push({
            type: 'treemap',
            layoutAlgorithm: 'squarified',
            data: treemapData
        });

        treeMapChart = genGraph('treeMapChart', treeMapOptions);

      }).catch(function(err){
        $log.log(err);
      });
    }

