    function getReachOverTime() {
      vm.listeningAnalysisService.getReachOverTime(vm.query, user.token).then(function(res){
        vm.processing++;
        try {
          var k = 1000,
              m = 1000000,
              b = 1000000000;
          var reachEvolutionLine = {
            title: {
              text: 'Reach Evolution',
              margin: 30
            },
            lang: {
                numericSymbols: ['k', 'M', 'B', 'T', 'P', 'E'],
                thousandsSep: ','
            },
            yAxis: {

                labels: {
                  formatter: function() {
                        var maxElement = this.axis.max;
                        if (maxElement > k && maxElement <= m) {
                            return (this.value / k).toFixed() + " K";
                        } else if (maxElement > m && maxElement <= b) {
                            return (this.value / m).toFixed() + " M";
                        } else if (maxElement > b) {
                            return (this.value / b).toFixed() + " B";
                        } else {
                            return (this.value) + " B";
                        }
                    },
                    format: '{value} Reach'
                },
                title: {
                    enabled: true,
                    text: 'Reach',
                    style: {
                        fontSize: '16px'
                    },
                    margin: 30
                }
            },
            plotOptions: {
                series: {
                    cursor: 'pointer',
                    point: {
                        events: {
                            click: function () {
                              vm.query.idate = moment(this.category + new Date().getFullYear())._d;
                              vm.query.edate = angular.copy(vm.query.idate);
                              vm.commanService.setQuery(vm.query);
                              $state.go('feed');
                                // alert('Category: ' + this.category + ', value: ' + this.y);
                            }
                        }
                    }
                }
            },
            xAxis: {
              categories: []
            },
            legend: {
              enabled: false
            },

            series: []
          };
          var reachOverTime = angular.copy(res.data.ppulse.data);
          reachEvolutionLine.xAxis.categories = reachOverTime.axis;
          reachEvolutionLine.series.push({
              name: 'Reach',
              data: reachOverTime.values,
              marker: {
                  fillColor: '#7bb5eb'
              },
              lineColor: '#7bb5eb',
              color: {
                linearGradient: { x1: 0, x2: 0, y1: 0, y2: 1 },
                stops: [
                    [0, 'rgba(124 ,181 , 236 ,0.5)'],
                    [1, 'rgba(124 ,181 , 236 ,0)']
                ]
              }
          });

          reachEvolutionLineChart = genGraph('reach-evolution', reachEvolutionLine);
        } catch(e) {
          $log.log(res);
        }
      }).catch(function(err){
        $log.log(err);
        vm.processing++;
      });
    }

