(function() {
  'use strict';

  angular
    .module('ppulse')
    .controller('SocialController', SocialController);

  /** @ngInject */
  function SocialController($timeout, toastr, $log, $state, authService, $mdDialog, $q, $auth, SocialService) {
    var vm = this,
      user = angular.copy(authService.getUser());

    vm.socialService = SocialService;

    vm.facebookConnected = false;
    vm.googleConnected = false;
    vm.instagramConnected = false;
    vm.twitterConnected = false;

    if(!user){ $state.go('login'); } else { init(); }
    var googleAuthProvider = new firebase.auth.GoogleAuthProvider();
    var twitterAuthProvider = new firebase.auth.TwitterAuthProvider();

    vm.authenticate = function(provider) {
      if(provider === 'twitter') {
        firebase.auth().signInWithPopup(twitterAuthProvider).then(function(result) {
          // This gives you a the Twitter OAuth 1.0 Access Token and Secret.
          // You can use these server side with your app's credentials to access the Twitter API.
          var token = result.credential.accessToken;
          var secret = result.credential.secret;
          var twitterUser = result.user;

          var accesTokenDetails = {
            'accountName': provider,
            'accessToken': token,
            'accessSecret': secret,
            'refreshToken': twitterUser.refreshToken
          };

          vm.socialService.integrateSocialAccount(accesTokenDetails, user.token)
          .then(function(res){
            vm.twitterConnected = true;
            user.linked_accounts.push(provider);
            authService.setUser();
          })
          .catch(function(err){
              $log.log(err);
          });
          // The signed-in user info.

          //console.log(user);
          // ...
        }).catch(function(error) {
          // Handle Errors here.
          console.log(error);
          
        });
      } else if(provider === 'google'){
        firebase.auth().signInWithPopup(googleAuthProvider).then(function(result) {
          // This gives you a the Twitter OAuth 1.0 Access Token and Secret.
          // You can use these server side with your app's credentials to access the Twitter API.
          var token = result.credential.accessToken;
          var secret = result.credential.secret;
          // The signed-in user info.
          var googleUser = result.user;
          var accesTokenDetails = {
            'accountName': provider,
            'accessToken': token,
            'refreshToken': googleUser.refreshToken
          };

          vm.socialService.integrateSocialAccount(accesTokenDetails, user.token)
          .then(function(res){
            vm.googleConnected = true;
            user.linked_accounts.push(provider);
            authService.setUser();
          })
          .catch(function(err){
              $log.log(err);
          });
          // ...
        }).catch(function(error) {
          // Handle Errors here.
          console.log(error);
        });
      } else if(provider === 'facebook' || provider === 'instagram'){
        $auth.authenticate(provider).then(function(res){
          var accesTokenDetails = {
            'accountName': provider,
            'accessToken': res.access_token
          };
          vm.socialService.integrateSocialAccount(accesTokenDetails, user.token).then(function(res){
            if(provider === 'facebook') vm.facebookConnected = true;
            else if(provider === 'instagram') vm.instagramConnected = true;
            user.linked_accounts.push(provider);
            authService.setUser();
          }).catch(function(err){
              $log.log(err);
          });
        }).catch(function(err){
            $log.log(err);
        });
      }
    };

    function init(){
      var socialList = angular.copy(user.linked_accounts);

      for(var i = 0;i < socialList.length;i++){
        if(socialList[i] === 'facebook'){
          vm.facebookConnected = true;
        } else if(socialList[i] === 'google'){
          vm.googleConnected = true;
        } else if(socialList[i] === 'instagram'){
          vm.instagramConnected = true;
        } else if(socialList[i] === 'twitter'){
          vm.twitterConnected = true;
        }
      }
    }
  }
})();

