(function() {
  'use strict';

  angular
    .module('ppulse')
    .controller('NotificationsController', NotificationsController);

  /** @ngInject */
  function NotificationsController($state, toastr, $log, authService, $mdDialog, moment, $q, NotificationService) {
    var vm = this,
      user = angular.copy(authService.getUser());

    vm.notificationService = NotificationService;
    vm.notificationsList = [];
    vm.defaultEmail = '';
    vm.defaultDesktop = '';
    if(!user) { $state.go('login'); }

    vm.notificationService.getNotificationsSettings(user.token).then(function(response){
      vm.notificationsList = angular.copy(response.data.ppulse.data);
    }).catch(function(error){
      $log.log(error);
    });



    vm.updateNotification = function(){
      vm.notificationService.updateNotificationSetting(vm.notificationsList, user.token)
      .then(function(response){
        if(response.data.ppulse.status === 'success'){
          toastr.success('Notifcation settings updated successfully!');
        } else {
          toastr.error('Unable to Update notifcation settings!');
        }
      }).catch(function(error){
        toastr.error('Unable to Update notifcation settings!');
        $log.log(error);
      });

    };

    vm.applyDefault = function(type) {
      if(type === 'email'){
        for(var i = 0;i<vm.notificationsList.length;i++){
          vm.notificationsList[i].mail_freq = angular.copy(vm.defaultEmail);
        }
      } else if(type === 'desktop'){
        for(var j = 0;j<vm.notificationsList.length;j++){
          vm.notificationsList[j].desk_freq = angular.copy(vm.defaultDesktop);
        }
      }
      vm.updateNotification();
    };

  }
})();

(function() {
  'use strict';

  angular
    .module('ppulse')
    .directive('stickyFilter', stickyFilter);

  /** @ngInject */
  function stickyFilter($document) {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/directives/stickyFilter/stickyFilter.html',
      link: function(scope, element, attrs){
        var doc = angular.element($document)[0].body;
        scope.showFilter = false;
        var trigger = function() {
          var scrollPos = angular.copy(doc.scrollTop);

          if(scrollPos > 100) {
            scope.showFilter = true;
            scope.$apply();
          } else {
            if(doc.style.position !== 'fixed'){
              scope.showFilter = false;
              scope.$apply();
            }
          }
        };
        angular.element($document).on('scroll', trigger);
      },
      scope: {
        filterData: '=',
        applyFilter: '=',
        cities: '='
      },
      controller: StickyFilterController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function StickyFilterController(moment, $scope, $log, $state, authService, $mdDialog, CommanService, $timeout) {
      var vm = this,
          user = angular.copy(authService.getUser());
      if(!user) { $state.go('login'); }

      vm.day = false;
      vm.week = true;
      vm.month = false;
      vm.apply = apply;
      vm.citySelect = citySelect;
      vm.commanService = CommanService;
      var todayDate = new Date(), debounceBool = true;
      vm.filterData.idate = moment().subtract(7, 'days')._d;
      vm.filterData.edate = todayDate;
      vm.showFilterClick = true;
      vm.alertName = '';

      var savedQuery = vm.commanService.getQuery();
      //vm.tempLanguages = angular.copy(vm.filterData.languages);

      if(savedQuery !== null){
        vm.filterData = angular.copy(savedQuery);
        //vm.tempLanguages = angular.copy(vm.filterData.languages);

        var start = moment(vm.filterData.edate);
        var end   = moment(vm.filterData.idate);
        var dateDiff = start.diff(end, 'days');
        if(dateDiff === 0){
          vm.day = true;
          vm.week = false;
        } else if(dateDiff === 6) {
          vm.day = false;
          vm.week = true;
        } else {
          vm.day = false;
          vm.week = false;
        }

        vm.month = false;
      }

      vm.selectedPeriod = function() {
        vm.filterData.edate = todayDate;
        if(vm.dateSelected === 'day'){
          vm.filterData.idate = moment().subtract(1, 'days')._d;
          vm.day = true;
          vm.week = false;
          vm.month = false;
        }
        else if(vm.dateSelected === 'week'){
          vm.filterData.idate = moment().subtract(7, 'days')._d;
          vm.day = false;
          vm.week = true;
          vm.month = false;
        }
        else if(vm.dateSelected === 'month'){
          vm.filterData.idate = moment().subtract(1, 'M')._d;
          vm.day = false;
          vm.week = false;
          vm.month = true;
        }
      };

      vm.customDate = function() {
        vm.day = false;
        vm.week = false;
        vm.month = false;
      };

      function citySelect() {
        for(var i = 0;i<vm.filterData.cities.length; i++) {
          if(vm.filterData.cities[i] === 'All' || vm.filterData.cities[i] === 'all'){
            vm.filterData.cities = [];
            for(var j = 0;j < vm.cities.length; j++){
              vm.filterData.cities.push(vm.cities[j]);
            }
            break;
          }
        }
      }

      $scope.$watch('vm.filterData.alertid', function(oldvalue, newValue) {
          if(vm.filterData.alertid) {
            vm.commanService.getAlertName(user.token, vm.filterData.alertid).then(function(res){
              vm.alertName = res.data.ppulse.data.name;
            }).catch(function(err){
              console.log(err);
            });
          }
      });

      $scope.$watch('vm.filterData.cities', function(oldvalue, newValue) {
          if(oldvalue !== newValue){
            //for(vm.filterData.cities)
            if(debounceBool) {
              vm.citySelect();
              vm.apply();
              debounceBool = false;
              $timeout(function () {
                debounceBool = true;
              }, 3000);
            } else {
              newValue = oldvalue;
            }
          }
      });

      function apply(filterName, filterValue){
        if(filterName === 'sources' || filterName === 'languages') {
          filterValue.value = !filterValue.value;
        }

        if(!Array.isArray(vm.filterData.alertid)){
          vm.commanService.setQuery(vm.filterData);
        }
        vm.applyFilter();
        vm.showFilterClick = false;
        $timeout(function () {
          vm.showFilterClick = true;
          $scope.$apply();
        }, 3000);
      }

    }
  }

})();

