(function() {
  'use strict';

  angular
    .module('ppulse')
    .controller('LoginController', LoginController);

  /** @ngInject */
  function LoginController(toastr, $log, $state, $stateParams, authService, $mdDialog, config, $timeout) {
    var vm = this;

    vm.showToastr = showToastr;
    vm.login = login;
    vm.newUser = newUser;
    vm.confirmReset = confirmReset;

    vm.forgotPassBool = false;
    vm.incorrect = false;
    vm.processing = false;
    vm.errorMsg = '';
    vm.credentials = {};

    if($stateParams.token && $stateParams.email) {
      vm.credentials.token = angular.copy($stateParams.token);
      vm.credentials.email = angular.copy($stateParams.email);
      vm.processing = true;
      vm.credentials.firebaseToken = angular.copy(config.firebaseToken);
      authService.activteUser(vm.credentials).success(function(res){
        vm.processing = false;

        if(res.ppulse.status === 'failed') {
          toastr.error('Something might be wrong', 'Unable to Activate!');
        } else {
          var incompleteValues = res.ppulse.data.incomplete;
          if(incompleteValues.length > 0){
            for(var i = 0 ;i < incompleteValues.length; i++){
              completeProfileToast(incompleteValues[i], i);
            }
          }
          authService.setUser(res.ppulse.data);
          toastr.success('Activated Successfully!', 'Welcome to People\'s Pulse');
          $state.go('dashboard.listening-analysis');
        }

      }).error(function(err){
        $log.log(err);
      });
    }

    function completeProfileToast(text, index) {
      var waitTime = (index+1) * 20000;
      $timeout(function () {
        toastr.success(text);
      }, waitTime);
    }

    function login() {
      vm.processing = true;
      vm.credentials.firebaseToken = config.firebaseToken;
      authService.login(vm.credentials).success(function(res){
        vm.processing = false;

        if(res.ppulse.status === 'failed') {
          vm.incorrect = true;
          vm.errorMsg = 'Username or Password is Incorrect.';
        } else {
          var incompleteValues = res.ppulse.data.incomplete;
          if(incompleteValues.length > 0){
            for(var i = 0 ;i < incompleteValues.length; i++){
              completeProfileToast(incompleteValues[i], i);
            }
          }
          authService.setUser(res.ppulse.data);
          //$log.log(res.ppulse.data);
          toastr.success('Have a good day!', 'Welcome '+ res.ppulse.data.u_name);
          $state.go('dashboard.listening-analysis');
        }

      }).error(function(err){
        $log.log(err);
      });
    }

    function newUser() {
      authService.setUser(undefined);
    }

    function confirmReset() {
      vm.processing = true;
      authService.forgotPass(vm.credentials).success(function(res){
        vm.processing = false;
        if(res.ppulse.status === 'failed'){
          vm.errorMsg = res.ppulse.message;
          vm.incorrect = true;
        }
        else {
          toastr.success('Success','Please check Your Email');
          vm.forgotPassBool = false;
          vm.incorrect = false;
        }
      }).error(function(err){
         $log.log(err);
      });
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }


  }
})();

