    function getTopics() {
      vm.insightService.getTopics(vm.query, user.token).then(function(res){
        // vm.processing++;
        var topics = angular.copy(res.data.ppulse.data);
        // reachEvolutionLine.xAxis[0].setCategories(reachOverTime.axis,true);
        // reachEvolutionLine.series[0].setData(reachOverTime.values,true);

        var treeMapChart = {
          colorAxis: {
              minColor: '#EFF6FD',
              maxColor: Highcharts.getOptions().colors[0]
          },
          legend: {
             enabled: false
          },
          series: [{
              type: 'treemap',
              layoutAlgorithm: 'squarified',
              data: []
          }],
          title: {
              text: 'Topics',
              margin: 30
          },
          plotOptions: {
              series: {
                  cursor: 'pointer',
                  point: {
                      events: {
                          click: function () {
                            vm.query.searchWord = angular.copy(this.name);
                            vm.commanService.setQuery(vm.query);
                            $state.go('feed');

                            // $log.log(this.name);
                              // alert('Category: ' + this.category + ', value: ' + this.y);
                          }
                      }
                  }
              }
          }
        };

        topics.sort(function(a, b) {
            return parseFloat(b.score) - parseFloat(a.score) ;
        });
        // topics.reverse();
        var colorCode = 1;
        for(var i = 0;i<topics.length;i++){
          try{
            if(topics[i].score !== topics[i-1].score)
              colorCode++;
          } catch(e){
            $log.log(e);
          }
          treeMapChart.series[0].data.push({'name': topics[i].topic, 'value': topics[i].score, 'colorValue': colorCode});

        }

        // for(var i =0;i<topics.length;i++){
        //   treeMapChart.series[0].data.push({'name': topics[i].topic, 'value': topics[i].score, 'colorValue': i+1});
        // }

        treeMapChart = genGraph('treeMapChart', treeMapChart);
        // genGraph('lineChart', lineChart);
      }).catch(function(err){
        $log.log(err);
      });
    }

