(function() {
  'use strict';

  angular
    .module('ppulse')
    .controller('FeedController', FeedController);

  /** @ngInject */
  function FeedController($state, $scope, CommanService, authService, $log, $mdDialog, FeedService, $sce, toastr, $q, $window) {
    var vm = this,
        user = angular.copy(authService.getUser());

    // Services
    vm.feedService = FeedService;
    vm.commanService = CommanService;

    // Functions
    vm.init = init;
    vm.showFilter = showFilter;
    vm.execQuery = execQuery;
    vm.feedClick = feedClick;
    vm.trustSrc = trustSrc;
    vm.openAlertPopup = openAlertPopup;
    vm.newAlertProceed = newAlertProceed;
    vm.tagMention = tagMention;
    vm.tagClick = tagClick;
    vm.stopPropagation = stopPropagation;
    vm.addAlert = addAlert;
    vm.addKeyordsAlert = addKeyordsAlert;
    vm.loadMore = loadMore;
    vm.getAlertData = getAlertData;
    vm.shareFacebook = shareFacebook;
    
    // Variables
    vm.hideOnLoad = true;
    vm.companyToMonitor = '';
    vm.processing = true;
    vm.showNewAlert = false;
    vm.feedData = [];
    vm.alertSection = 'main';
    vm.optionalKeywords = [];
    vm.excludedKeywords = [];
    vm.requiredKeywords = [];
    vm.mentionCount = 0;
    vm.showLoadmore = false;
    vm.sortByType = 'c_timestamp';
    vm.sortByDirection = 'DESC';
    vm.inboxCount = 0;
    vm.favCount = 0;
    vm.archiveCount = 0;
    vm.trashCount = 0;
    vm.spamCount = 0;

    vm.newAlert = {
      reqKeys: [],
      optionKeys: [],
      exluKeys : [],
      compName: '',
      compWeb: '',
      compTwitter: '',
      compFacebook: ''
    };
    vm.query = {
      alertid : null,
      idate: '2017-01-15',
      edate: '2017-04-15',
      sources: [],
      languages: [],
      pos: true,
      neg: true,
      infulencerMin: 0,
      infulencerMax: 100,
      searchWord: '',
      day: '',
      myLastTimestamp: 0,
      loadMore: '',
      cities: [],
      tag: ''
    };

    // User logged in
    if(!user){ $state.go('login'); } else { vm.init(); }

    function showFilter(ev) {
      $mdDialog.show({
        contentElement: '#filter',
        parent: angular.element(document.body),
        targetEvent: ev,
        clickOutsideToClose: true
      });
    }

    function shareFacebook(url) {
      //FB.api('/me/feed', 'post', {message: 'Hello, world!'});

        FB.ui({
          method: 'share',
          href: url,
        }, function(response){
          console.log();
        });


    }

    function getAlerts() {
      var deferred = $q.defer();
      vm.commanService.getAlerts(user.token).then(function(res){
        vm.alerts = angular.copy(res.data.ppulse.data);
        if(vm.query.alertid === null) {
          vm.query.alertid = vm.alerts[0].alertid;
        }
        deferred.resolve();
      }).catch(function(err){
        deferred.reject(err);
      });
      return deferred.promise;
    }

    function getAlertData(sortDirection) {
      vm.processing = true;

      if(sortDirection){
        vm.sortByDirection = sortDirection;
      }

      vm.query.sortBy = angular.copy(vm.sortByType +' '+vm.sortByDirection);
      vm.feedService.getAlertData(vm.query, user.token).then(function(res){
        vm.processing = false;
        vm.hideOnLoad = false;
        vm.feedData = angular.copy(res.data.ppulse.data);
        var tagsCount =  res.data.ppulse.message;
        if(vm.feedData.length === 50) {
          vm.showLoadmore = true;
        } else {
          vm.showLoadmore = false;
        }

        for(var i = 0; i < tagsCount.length; i++) {

          if(tagsCount[i].name === 'unread') {
            vm.inboxCount = tagsCount[i].count;
          } else if(tagsCount[i].name === 'favourite'){
            vm.favCount = tagsCount[i].count;
          } else if(tagsCount[i].name === 'archive'){
            vm.archiveCount = tagsCount[i].count;
          } else if(tagsCount[i].name === 'trash'){
            vm.trashCount = tagsCount[i].count;
          } else if(tagsCount[i].name === 'spam'){
            vm.spamCount = tagsCount[i].count;
          }
        }

        for(i = 0; i < vm.alerts.length; i++) {
          if(vm.alerts[i].alertid === vm.query.alertid) {
            vm.mentionCount = angular.copy(vm.alerts[i].count);
            break;
          }
        }

      }).catch(function(err) {
        $log.log(err);
      });
    }

    function feedClick(feed, ev) {
      var url = feed.open_url,
          source = feed.source,
          elementDisplay = ev.currentTarget.parentElement;

      if(elementDisplay.querySelector("#url-frame").className === 'hide-iframe') {
        elementDisplay.querySelector("#url-frame").className = "show-iframe";
        if(source === 'Twitter'){
          vm.feedService.getHTMLTwitter(url,user.token).then(function(res){
            elementDisplay.querySelector("#webPreview").innerHTML = res.data.ppulse.data.html;
          }).catch(function(err){
            elementDisplay.querySelector("#webPreview").innerHTML = '<h4 class="col-xs-12">Tweet was removed.</h4>';
          });
        } else if(source === 'Facebook'){
          feed.openfb = true;
        } else {
          vm.feedService.getHtmlPage(url, user.token).then(function(res){
            elementDisplay.querySelector("#webPreview").innerHTML = '<iframe src="'+url+'"/>';
          }).catch(function(err){
            elementDisplay.querySelector("#webPreview").innerHTML = '<h4 class="col-xs-12">Click above link to preview in a new tab.</h4>';
          });
        }
      }
      else {
        elementDisplay.querySelector("#url-frame").className = "hide-iframe";
      }

    }

    function execQuery() {
      getAlertData();
      vm.commanService.setQuery(vm.query);
      $mdDialog.hide();
    }

    function trustSrc(src) {
      return $sce.trustAsResourceUrl(src);
    }

    function openAlertPopup() {
      vm.showNewAlert = !vm.showNewAlert;
    }

    function addAlert() {
      vm.newAlert.reqKeys = [];
      vm.newAlert.optionKeys = [];
      vm.newAlert.exluKeys = [];
      vm.feedService.addAlert(vm.newAlert,user.token).then(function(res){
        vm.showNewAlert = false;
        getAlerts();
        vm.alertSection = 'main';
        vm.newAlert = {
          reqKeys: [],
          optionKeys: [],
          exluKeys : [],
          compName: '',
          compWeb: '',
          compTwitter: '',
          compFacebook: ''
        };
        toastr.success('Alert Added Successfully');
      }).catch(function(err){
        $log.log(err);
      });
    }

    function addKeyordsAlert() {
      if(vm.newAlert.reqKeys.length > 0){
        vm.newAlert.compName = '';
        vm.newAlert.compWeb = '';
        vm.newAlert.compTwitter = '';
        vm.newAlert.compFacebook = '';
        vm.newAlert.alertType = 'keyword';
        vm.feedService.addAlert(vm.newAlert,user.token).then(function(res){
          vm.showNewAlert = false;
          vm.alertSection = 'main';
          getAlerts();
          vm.newAlert = {
            reqKeys: [],
            optionKeys: [],
            exluKeys : [],
            compName: '',
            compWeb: '',
            compTwitter: '',
            compFacebook: ''
          };
          toastr.success('Keyword Alert Added Successfully');
        }).catch(function(err){
          $log.log(err);
        });
      }
    }

    function newAlertProceed() {
      if(vm.newAlert.compName){
        if(vm.alertSection==='section1') {
          vm.newAlert.alertType = 'Company';
        } else {
          vm.newAlert.alertType = 'Competitor';
        }
        vm.alertSection = 'section12';

      }
    }

    function tagMention(selectedFeed, tagName, ev) {
      ev.stopPropagation();
      var tagDetails = {
        mention_id : selectedFeed.id,
        tag: tagName
      };

      if(selectedFeed.tag === tagName) {
        tagDetails.tag = 'unread';

        selectedFeed.tag = 'unread';
        vm.feedService.unTagMention(tagDetails, user.token).then(function(res){
          //selectedFeed.tag = 'unread';
          vm.inboxCount++;

          if(tagName === 'favourite'){
            vm.favCount--;
          } else if(tagName === 'archive'){
            vm.archiveCount--;
          } else if(tagName === 'trash'){
            vm.trashCount--;
          } else if(tagName === 'spam'){
            vm.spamCount--;
          }
        }).catch(function(err){
          $log.log(err);
          selectedFeed.tag = tagName;
        });
      } else {
        selectedFeed.tag = tagName;
        vm.feedService.tagMention(tagDetails,user.token).then(function(res){
          selectedFeed.tag = tagName;
          vm.inboxCount--;
          if(tagName === 'favourite'){
            vm.favCount++;
          } else if(tagName === 'archive'){
            vm.archiveCount++;
          } else if(tagName === 'trash'){
            vm.trashCount++;
          } else if(tagName === 'spam'){
            vm.spamCount++;
          }
        }).catch(function(err){
          $log.log(err);
        });
      }
    }

    function tagClick(tagName) {
      vm.query.tag = angular.copy(tagName);
      getAlertData();
    }

    function stopPropagation($event) {
      $event.stopPropagation();
    }

    function loadMore() {
      vm.query.myLastTimestamp = vm.feedData[vm.feedData.length];
      // vm.processing = true;
      vm.feedService.getAlertData(vm.query, user.token).then(function(res){
        vm.processing = false;
        vm.hideOnLoad = false;
        if(res.data.ppulse.data.length === 50){
          vm.showLoadmore = true;
        } else {
          vm.showLoadmore = false;
        }

        vm.feedData = vm.feedData.concat(angular.copy(res.data.ppulse.data));

      }).catch(function(err){
        $log.log(err);
      });
    }

    function getCities() {
      var deferred = $q.defer();

      vm.commanService.getCities().then(function(res) {
        vm.cities = angular.copy(res.data.ppulse.data);
        deferred.resolve();
      }).catch(function(err){
        deferred.reject(err);
      });

      return deferred.promise;
    }

    function getLanguages() {
      var deferred = $q.defer();
      vm.commanService.getLanguages().then(function(res){
        if(vm.query.languages.length === 0) {
          vm.query.languages = angular.copy(res.data.ppulse.data.languages);
        }
        deferred.resolve();
      }).catch(function(err){
        deferred.reject(err);
      });
      return deferred.promise;
    }

    function getSources() {
      var deferred = $q.defer();
      vm.commanService.getSources().then(function(res){
        if(vm.query.sources.length === 0) {
          vm.query.sources = angular.copy(res.data.ppulse.data.sources);
        }
        deferred.resolve();
      }).catch(function(err){
        deferred.reject(err);
      });
      return deferred.promise;
    }

    function getAlertsLimit() {
      vm.feedService.getAlertsLimit(user.token).then(function(res){
        if(res.data.ppulse.data.remaining === 0){
          vm.alertSection = 'limit';
        }
      }).catch(function(err){
        $log.log(err);
      });
    }

    function init(){

      var savedQuery = vm.commanService.getQuery();
      if(savedQuery !== null) {
        vm.query = angular.copy(savedQuery);
      }

      var getCitiesPromise = getCities();
      var getAlertsPromise = getAlerts();
      var getLanguagesPromise = getLanguages();
      var getSourcesPromise = getSources();

      $q.all([getCitiesPromise, getAlertsPromise, getLanguagesPromise, getSourcesPromise]).
      then(function (response) {
        getAlertData();
      })
      .catch(function (errorResponse) {
        $log.log(errorResponse);
      });

      getAlertsLimit();
    }

  }
})();

