(function() {
  'use strict';

  angular
    .module('ppulse')
    .controller('CinematicViewController', CinematicViewController);

  /** @ngInject */
  function CinematicViewController($scope, CommanService, ListeningAnalysisService, authService, $timeout, $log, $mdDialog, $sce, toastr, $document, CinematicApiService, Fullscreen, CinematicDataService) {
    var vm = this,
        user = angular.copy(authService.getUser()),
        volumeOvertimeChart,
        sentimentChart,
        webPrintReachChart,
        facebookImpressionChart,
        googleAvgSessionChart,
        googleBounceRateChart,
        googlePageViewsChart;

    vm.commanService = CommanService;
    vm.cinematicApiService = CinematicApiService;
    vm.listeningAnalysisService = ListeningAnalysisService;
    vm.cinematicDataService = CinematicDataService;

    vm.goFullscreen = goFullscreen;
    vm.query = vm.commanService.getQuery();
    vm.init = init;
    vm.isFullscreen = false;
    vm.citiesHash = {};
    vm.mentionByCities = [];
    vm.loading = true;

    if(!user){ $state.go('login'); } else {
      if(vm.query === null){
        getAlerts();
      } else {
        vm.init();
      }
    }

    var consoleError = function(err) {
      $console.log(err);
    };

    function getAlerts() {
      vm.commanService.getAlerts(user.token).then(function(res){
        try {
          vm.alerts = angular.copy(res.data.ppulse.data);
          vm.query = {};
          vm.query.alertid = vm.alerts[0].alertid;
          vm.init();
        } catch(e) {
          $log.log(res);
        }
      }).catch(consoleError);
    }

    function getAlertName(){
      vm.commanService.getAlertName(user.token, vm.query.alertid).then(function(res){
        vm.alertName = res.data.ppulse.data.name;
      }).catch(consoleError);
    }

    function genGraph(chartName, json) {
      return Highcharts.chart(chartName, json);
    }

    function cinematicFacebookMetrics() {
      vm.cinematicApiService.cinematicFacebookMetrics(user.token, vm.query.alertid)
      .then(function(res){
        vm.facebookData = res.data.ppulse.data;
        var facebookGraph = vm.cinematicDataService.getFacebookGraphSm();

        try {
          var facebookImpression = angular.copy(res.data.ppulse.data.impressionsGraph);
          facebookGraph.series.push({
              name: 'Mentions',
              data: facebookImpression.values,
              marker: {
                  enabled: false
              },
              color: {
                linearGradient: { x1: 0, x2: 0, y1: 0, y2: 1 },
                stops: [
                    [0, 'rgba(43, 97 , 157,0.5)']
                ]
              },
              lineColor: '#7bb5eb'
          });
          facebookImpressionChart = genGraph('facebook-impression', facebookGraph);
        } catch(e) {
          $log.log(res);
        }

        try {
          var facebookEngagement = angular.copy(res.data.ppulse.data.engagementGraph);
          facebookGraph.series = [];
          facebookGraph.series.push({
              name: 'Mentions',
              data: facebookEngagement.values,
              marker: { enabled: false },
              color: {
                linearGradient: { x1: 0, x2: 0, y1: 0, y2: 1 },
                stops: [
                    [0, 'rgba(43, 97 , 157,0.5)']
                ]
              },
              lineColor: '#7bb5eb'
          });
          facebookEngagementChart = genGraph('facebook-engagment', facebookGraph);
        } catch(e) {
          $log.log(res);
        }
      }).catch(consoleError);
    }

    function cinematicGoogleAnalytics() {
      vm.cinematicApiService.cinematicGoogleAnalytics(user.token, vm.query.alertid)
      .then(function(res){
        vm.googleData = res.data.ppulse.data;
        vm.loading = false;
        var googleGraph = vm.cinematicDataService.getGoogleGraphSm();
        var googleAvgSession = angular.copy(res.data.ppulse.data.avgSessionGraph);
        var googleBounceRate = angular.copy(res.data.ppulse.data.bounceRateGraph);
        var googlePageViews = angular.copy(res.data.ppulse.data.pageViewsGraph);

        try {
          googleGraph.series.push({
              name: 'Mentions',
              data: googleAvgSession.values,
              marker: {
                  enabled: false
              },
              color: {
                linearGradient: { x1: 0, x2: 0, y1: 0, y2: 1 },
                stops: [
                    [0, 'rgba(43, 97 , 157,0.5)']
                ]
              },
              lineColor: '#7bb5eb'
          });
          googleAvgSessionChart = genGraph('google-avg-session', googleGraph);
        } catch(e) {
          $log.log(res);
        }
        try {
          googleGraph.series = [];
          googleGraph.series.push({
              name: 'Mentions',
              data: googleBounceRate.values,
              marker: {
                  enabled: false
              },
              color: {
                linearGradient: { x1: 0, x2: 0, y1: 0, y2: 1 },
                stops: [
                    [0, 'rgba(43, 97 , 157,0.5)']
                ]
              },
              lineColor: '#7bb5eb'
          });
          googleBounceRateChart = genGraph('google-bounce-rate', googleGraph);
        } catch(e) {
          $log.log(res);
        }
        try {
          googleGraph.series = [];
          googleGraph.series.push({
              name: 'Mentions',
              data: googlePageViews.values,
              marker: {
                  enabled: false
              },
              color: {
                linearGradient: { x1: 0, x2: 0, y1: 0, y2: 1 },
                stops: [
                    [0, 'rgba(43, 97 , 157,0.5)']
                ]
              },
              lineColor: '#7bb5eb'
          });
          googlePageViewsChart = genGraph('google-page-views', googleGraph);
        } catch(e) {
          $log.log(res);
        }
      }).catch(consoleError);
    }

    function cinematicTopCities() {
      vm.cinematicApiService.cinematicTopCities(user.token, vm.query.alertid)
      .then(function(res){
        vm.mentionByCities = res.data.ppulse.data.citydata;
        var minBulletSize = 5;
        var maxBulletSize = 15;
        var min = Infinity;
        var max = -Infinity;
        var maxSquare = maxBulletSize * maxBulletSize * 2 * Math.PI;
        var minSquare = minBulletSize * minBulletSize * 2 * Math.PI;
        var value;
        for ( var i = 0; i < vm.mentionByCities.length; i++ ) {
          if(vm.mentionByCities[i].name !== 'untagged'){
            value = vm.mentionByCities[ i ].mentions;
            if ( value < min ) {
              min = value;
            }
            if ( value > max ) {
              max = value;
            }

            vm.citiesHash[vm.mentionByCities[i].name] = {mentions : vm.mentionByCities[ i ].mentions, name : vm.mentionByCities[ i ].name};
          }
        }
        for (i = 0; i < vm.mentionByCities.length; i++ ) {
            var dataItem = vm.mentionByCities[ i ];
            value = dataItem.mentions;
            // calculate size of a bubble
            var square = ( value - min ) / ( max - min ) * ( maxSquare - minSquare ) + minSquare;
            if ( square < minSquare ) {
              square = minSquare;
            }

            var size = Math.sqrt( square / ( Math.PI * 2 ) );

            if(size === Infinity || size === -Infinity ){
              size = 0;
            }
            vm.mentionByCities[i].markerSize = size;
            vm.citiesHash[vm.mentionByCities[i].name] = {size : size};
        }

      }).catch(consoleError);
    }

    function cinematicTwitterMatrices() {
      vm.cinematicApiService.cinematicTwitterMatrices(user.token, vm.query.alertid)
      .then(function(res){
        vm.twitterData = res.data.ppulse.data;
      }).catch(consoleError);
    }

    function cinematicTopStats() {
      vm.cinematicApiService.cinematicTopStats(user.token, vm.query.alertid)
      .then(function(res){
        vm.topStats = res.data.ppulse.data;
      }).catch(consoleError);
    }

    function cinematicSentimentAnalysis() {
      vm.cinematicApiService.cinematicSentimentAnalysis(user.token, vm.query.alertid)
      .then(function(res){
        var sentimentOverTime = angular.copy(res.data.ppulse.data.sentimentGraph);
        var sentimentGraph = vm.cinematicDataService.getSentimentGraph();

        try {
          sentimentGraph.series.push({showInLegend: false, name: 'Negative', data: sentimentOverTime.neg, borderColor: '#D04436'});
          sentimentGraph.series.push({showInLegend: false, name: 'Positive', data: sentimentOverTime.pos, borderColor: '#2E639B'});
          sentimentGraph.xAxis.categories = angular.copy(sentimentOverTime.axis);
          sentimentChart = genGraph('sentiment-graph',sentimentGraph);

        } catch(e){
          $log.log(res);
        }

      }).catch(consoleError);
    }

    function cinematicWebPrintReach() {
      vm.cinematicApiService.cinematicWebPrintReach(user.token, vm.query.alertid)
      .then(function(res){
        try {
          var webPrintReachGraph = vm.cinematicDataService.getWebPrintReachGraph();
          var webPrintReach = angular.copy(res.data.ppulse.data.reachGraph);
          webPrintReachGraph.series.push({
              name: 'Mentions',
              data: webPrintReach.web,
              marker: {
                  fillColor: '#e53f6a'
              },
              lineColor: '#e53f6a',
              color: {
                linearGradient: { x1: 0, x2: 0, y1: 0, y2: 1 },
                stops: [
                    [0, 'rgba(229 ,63,106, 0.5)']
                ]
              }
          });
          webPrintReachGraph.series.push({
              name: 'Mentions',
              data: webPrintReach.print,
              marker: {
                  fillColor: '#7bb5eb'
              },
              lineColor: '#7bb5eb',
              color: {
                linearGradient: { x1: 0, x2: 0, y1: 0, y2: 1 },
                stops: [
                    [0, 'rgba(43, 97 , 157,0.5)']
                ]
              }
          });
          webPrintReachGraph.xAxis.categories = webPrintReach.axis;
          webPrintReachChart = genGraph('web-print-reach', webPrintReachGraph);
        } catch(e) {
          $log.log(res);
        }
      }).catch(consoleError);
    }

    function goFullscreen() {
      vm.isFullscreen = !vm.isFullscreen;
    }

    function refreshPage(){
      $timeout(function () {
        vm.init();
      }, 5000*60*60);
    }

    function init() {
      refreshPage();
      getAlertName();
      cinematicGoogleAnalytics();
      cinematicTopCities();
      cinematicTwitterMatrices();
      cinematicTopStats();
      cinematicFacebookMetrics();
      cinematicSentimentAnalysis();
      cinematicWebPrintReach();
    }

  }
})();

