(function () {
  angular.module('ppulse')
    .service('authService', function ($http, config,$localStorage) {
      var user = {};
      this.login = function (credentials) {
        return $http.post(config.apiUrl + '/ppulse/login.php', credentials);
      };

      this.logout = function (token) {
        $localStorage.user = undefined;
        var req = {
          method: 'post',
          url: config.apiUrl + '/ppulse/logout.php',
          headers: {
            'access-token' : token
          }
        };
        return $http(req);
      };

      this.authorizationStatus = function (token) {
        var req = {
          method: 'post',
          url: config.apiUrl + '/ppulse/checkLogin.php',
          headers: {
            'access-token' : token
          }
        };
        return $http(req);
      };

      this.activteUser = function (credentials) {
        var req = {
          method: 'post',
          url: config.apiUrl + '/ppulse/verifyUserEmail.php',
          data: credentials
        };
        return $http(req);
      };

      this.forgotPass = function (credentials) {
        var req = {
          method: 'post',
          url: config.apiUrl + '/ppulse/forgotPassword.php?email='+credentials.username,
          data: credentials
        };
        return $http(req);
      };

      this.setUser = function(userDetails) {
        $localStorage.user = angular.copy(userDetails);
      };

      this.getUser = function() {
        return $localStorage.user;
      };
    });
})();

(function() {
  'use strict';

  angular
    .module('ppulse')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController($window, $scope, $log, $state, authService, toastr, $location, $anchorScroll) {
      var vm = this,
          user = angular.copy(authService.getUser());

      vm.userImg = './assets/images/jasmine.png';

      if(!user) {
        $state.go('login');
      } else {
        vm.token = user.token;
        vm.userImg = user.u_picture;
        vm.$state = $state;
        authService.authorizationStatus(user.token).then(function(res){
          if(res.data.ppulse.status==='failed'){
            toastr.warning('Your Session has expired!');
            $state.go('login');
          }
        }).catch(function(err){
          $log.log(err);
        });
      }

      vm.logout = function() {
        authService.logout(user.token).then(function(res){
            $state.go('login');
        }).catch(function(err){
          $log.log(err);
        });
      };

      $scope.$on('$stateChangeStart', function(){
          console.log('change Start');
          $anchorScroll();
      });

      $scope.$on('$stateChangeSuccess', stateChangeSuccess);

      function stateChangeSuccess() {
        // Collapsing the menu after navigation
        // vm.isCollapsed = false;
        user = angular.copy(authService.getUser());
        $window.ga('send', 'pageview', { page: $location.url() });

        if(!user) {
          $state.go('login');
        } else {

          if(arguments[1].name !== 'login'){
            authService.authorizationStatus(user.token).then(function(res){
              if(res.data.ppulse.status==='failed'){
                toastr.warning('Your Session has expired!');
                $state.go('login');
              }
            }).catch(function(err){
              $log.log(err);
            });
          }
        }
      }
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('ppulse')
    .directive('acmeMalarkey', acmeMalarkey);

  /** @ngInject */
  function acmeMalarkey(malarkey) {
    var directive = {
      restrict: 'E',
      scope: {
        extraValues: '='
      },
      template: '&nbsp;',
      link: linkFunc,
      controller: MalarkeyController,
      controllerAs: 'vm'
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;
      var typist = malarkey(el[0], {
        typeSpeed: 40,
        deleteSpeed: 40,
        pauseDelay: 800,
        loop: true,
        postfix: ' '
      });

      el.addClass('acme-malarkey');

      angular.forEach(scope.extraValues, function(value) {
        typist.type(value).pause().delete();
      });

      watcher = scope.$watch('vm.contributors', function() {
        angular.forEach(vm.contributors, function(contributor) {
          typist.type(contributor.login).pause().delete();
        });
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function MalarkeyController($log, githubContributor) {
      var vm = this;

      vm.contributors = [];

      activate();

      function activate() {
        return getContributors().then(function() {
          $log.info('Activated Contributors View');
        });
      }

      function getContributors() {
        return githubContributor.getContributors(10).then(function(data) {
          vm.contributors = data;

          return vm.contributors;
        });
      }
    }

  }

})();

(function() {
  'use strict';

  angular
    .module('ppulse')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

